		      <p>给你分享一个好平台</p>
		    </div>
		  </div>
		  <div class="demand-content brt" style="padding:8px 15px;height:auto;">
		    <div class="share_ewm" style="text-align: center;">
		      <img height="120vw" :src="share_ewm" alt="" />
			  <!-- <img height="120vw" src="../../dist/img/tianjishe.jpg" alt="" /> -->
		    </div>
		    <p class="tjs_fx" style="text-align: center;">
		      长按识别二维码马上注册<br />新注册用户可获新手奖励
		    </p>
		  </div>
		  <div class="lianx">
		    <div class="share_logo">
		      <img src="../../../public/share_logo.png" alt="" />
		    </div>
		    <div class="tjs_js">
		      数万家制造企业都在用的供应链对接平台，快来注册吧~
		    </div>
		  </div>
		</div>
		<!-- 邀请好友 -->
		<van-row type="flex" justify="space-between" class="agent_head" align="center ">
		  <van-col span="18" offset="2">你好，{{agentName}}！</van-col>
		  <van-col span="6">
			  <div class="yqm" @click="share">
			  	<p><i class="iconfont iconerweima"></i><br /><span>我的邀请码</span></p>
			  </div>
		  </van-col>
		</van-row>
		<van-tabs @change="onTabChange" v-model="activeTabIndex">
		  <van-tab title="意向客户" >
			  <van-row type="flex" justify="space-around" class="box" v-for="(item,index) in Customers" :key="index">
			    <van-col span="22" class="tab_content">
					<p class="info_title">{{item.remark}}</p>
					<div class="tab_info">
						<p class="info_title">{{item.title}}</p>
						<p><i class="iconfont iconyixiangren"></i>意向人：{{item.username}}（邀请人：{{item.inviter}}）</p>
						<p><i class="iconfont iconcontact"></i>联系电话：{{item.phone}}</p>
						<p><i class="iconfont iconshijian"></i>登记时间：{{item.datetime}}</p>
					</div>
					<van-row gutter="20" type="flex" justify="space-around" class="info_btn">
					  <van-col span="10" class="lx" @click="callPhone2(item)" v-if="item.is_contract == 0">马上联系</van-col>
					  <van-col span="10" class="lx"  v-else style="background-color: #7F7F7F;" @click="callPhoneTow(item.phone)">已联系</van-col>
					  <van-col span="10" class="ck" @click="gotoWorkshop(item.house_id)">查看房源</van-col>
					</van-row>
				</van-col>
			  </van-row>
			  <div class="no-content"  v-if="Customers.length ==0 ">
			    <img src="../../assets/no-content.png" alt="" />
			    <p><strong>暂无意向客户</strong></p>
			    <p>暂无意向客户，快去看看吧~</p>
			  </div>
		  </van-tab>
		  <van-tab title="委托找房" >
			  <van-row type="flex" justify="space-around" class="box" v-for="(item,index) in data" :key="index">
			    <van-col span="22" class="tab_content">
					<!-- <p class="info_title">{{item.remark}}</p> -->
					<div class="tab_info wtzf">
						<p><i class="iconfont iconyixiangren"></i>意向人：{{item.username}}（邀请人：{{item.inviter}}）</p>
						<p><i class="iconfont iconcontact"></i>联系电话：{{item.phone}}</p>
						<p><i class="iconfont iconbiaodiandidian"></i>目标位置：{{item.location}}</p>
						<p><i class="iconfont iconmubiaoyusuan"></i>目标预算：{{item.budget}}元 / m²</p>
						<p><i class="iconfont iconmubiaomianji"></i>目标面积：{{item.area}}m²</p>
						<p><i class="iconfont iconwodexuqiu"></i>需求：{{item.remark}}</p>
						<p><i class="iconfont iconshijian"></i>登记时间：{{item.datetime}}</p>
					</div>
					<van-row gutter="20" type="flex" justify="space-between" class="info_btn">
					  <van-col span="10" class="lx" @click="callPhone(item)" v-if="item.is_contract == 0">马上联系</van-col>
					  <van-col span="10" class="lx" v-else style="background-color: #7F7F7F;" @click="callPhoneTow(item.phone)">已联系</van-col>
					  <!-- <van-col span="10" class="ck" @click="gotoWorkshop(item.record_id)">查看房源</van-col> -->
					</van-row>
				</van-col>
			  </van-row>
			  <div class="no-content"  v-if="data.length ==0 ">
			    <img src="../../assets/no-content.png" alt="" />
			    <p><strong>暂无委托找房</strong></p>
			    <p>暂无委托找房，快去看看吧~</p>
