</template>

<script>
import html2canvas from "html2canvas";
import { mapActions, mapGetters } from "vuex";
import {Tabs,Tab,Col, Row} from "vant";	
import {houseList,agenHouselist,findHousecontract,contractHouseCustomers,markContract,isHouseAgent} from "@/api/v4.js"
import { userdShare} from "@/api/demand.js";
export default {
  data() {
    return {
      activeTabIndex: 0,
	  data:[],
	  Houselist:[],
	  UserList:[],
	  Customers:[],
	  posterImg:"",
	  dialogTableVisible:false,
	  share_ewm: "",
	  agentName:null
    }
  },
  components:{
	[Tabs.name]: Tabs,
    [Tab.name]: Tab,	
	[Col.name]: Col,
	[Row.name]: Row,
  },
  created() {
	  this.fetchUserInfo(this.userInfo.id);
	  // if(!this.userInfo.id){
		 //  this.$router.replace("/identity")
	  // }
	this.agentName = sessionStorage.getItem("agentName")
  	userdShare({ user_id: this.userInfo.id }).then(res => {
  	  this.share_ewm = res.url;
  	});
	isHouseAgent().then(res=>{
		this.agentName = res.agent_name
	})
  },
  mounted() {
	this.getData();
  	let initTabIndex = 0;
  	switch (this.tabName) {
  	  case "intention":
  	    initTabIndex = 0;
  	    break;
  	  case "entrust":
  	    initTabIndex = 1;
  	    break;
  	  case "housing":
  	    initTabIndex = 2;
  	    break;
  	  default:
  	    break;
  	}
  	this.activeTabIndex = initTabIndex;
  },
  methods:{
	  ...mapActions(["fetchUserInfo"]),
	// 点击返回按钮
	onTabChange(index) {
	  let name = "intention";
	  switch (index) {
	    case 0:
	      name = "intention";
	      break;
	    case 1:
	      name = "entrust";
	      break;
	    case 2:
	      name = "housing";
	      break;
	    default:
	      break;
	  }
	  this.$router.replace({ name: "agentList", query: { tab: name } });
	},
	customerList(){
		 this.$router.push("/customerList")
	},
	// 调用拨号功能
	callPhone (item) {
		console.log(item.phone,item.record_id)
	    window.location.href = 'tel://' + item.phone
		findHousecontract({
			record_id:item.record_id
		}).then(res=>{
			this.getData()
		})
	},
	// 调用拨号功能
	callPhone2(item) {
		console.log(item.phone,item.record_id)
	    window.location.href = 'tel://' + item.phone
		markContract({
			record_id:item.record_id
		}).then(res=>{
			this.getData()
		})
	},
	
	callPhoneTow(phone){
		window.location.href = 'tel://' + phone
		this.getData()
	},
	gotohouse(id){
		this.$router.push(`/customerList/${id}`);
	},
	gotoWorkshop(id){
		console.log(id)
		this.$router.push(`/workshop/details/${id}`);
	},
	getData(){
		houseList().then(res=>{
			this.data = res
		})
		agenHouselist().then(res=>{
			this.Houselist = res
		})
		contractHouseCustomers().then(res=>{
			this.Customers = res
		})
	},
	hideYY() {
	  this.dialogTableVisible = false;
	},
	close() {
	  this.dialogTableVisible = false;
	},
	share(){
		// this.showBG = true;
		// console.log(href?id=this.userInfo.id)
		let user_id = this.userInfo.id;
		// this.$emit("show",true)
		// 生成海报
		var copyDom = this.$refs.posterHtml; //要保存的dom
		var width = copyDom.offsetWidth; //dom宽
		var height = copyDom.offsetHeight; //dom高
		var scale = 2; //放大倍数
		html2canvas(copyDom, {
		  async: true,
		  useCORS: true,
		  allowTaint: true,
		  dpi: 326,
		  scale: 2,
		  taintTest: true
		}).then(canvas => {
		  let dataURL = canvas
		    .toDataURL("image/png")
		    .replace("image/png", "image/octet-stream");
		  this.posterImg = dataURL;
		  if (this.posterImg !== "") {
		    this.dialogTableVisible = true;
		  }
		});
	}
  },
  computed:{
	  ...mapGetters(["userInfo"]),
	  tabName() {
		return this.$route.query.tab || "intention";
	  },
  }
}
</script>

